<template>
  <div class="register-container">
    <div class="register-form">
      <h2>ÌöåÏõêÍ∞ÄÏûÖ</h2>
      <form @submit.prevent="handleRegister">
        <div class="form-group">
          <label for="username">ÏÇ¨Ïö©ÏûêÎ™Ö</label>
          <input
            type="text"
            id="username"
            v-model="userData.username"
            required
            placeholder="ÏÇ¨Ïö©ÏûêÎ™ÖÏùÑ ÏûÖÎ†•ÌïòÏÑ∏Ïöî"
          />
        </div>
        <div class="form-group">
          <label for="email">Ïù¥Î©îÏùº</label>
          <input
            type="email"
            id="email"
            v-model="userData.email"
            required
            placeholder="Ïù¥Î©îÏùºÏùÑ ÏûÖÎ†•ÌïòÏÑ∏Ïöî"
          />
        </div>
        <div class="form-group">
          <label for="displayName">ÌëúÏãú Ïù¥Î¶Ñ</label>
          <input
            type="text"
            id="displayName"
            v-model="userData.displayName"
            placeholder="ÌëúÏãú Ïù¥Î¶ÑÏùÑ ÏûÖÎ†•ÌïòÏÑ∏Ïöî (ÏÑ†ÌÉùÏÇ¨Ìï≠)"
          />
        </div>
        <div class="form-group">
          <label for="password">ÎπÑÎ∞ÄÎ≤àÌò∏</label>
          <input
            type="password"
            id="password"
            v-model="userData.password"
            required
            placeholder="ÎπÑÎ∞ÄÎ≤àÌò∏Î•º ÏûÖÎ†•ÌïòÏÑ∏Ïöî"
          />
        </div>
        <div class="form-group">
          <label for="confirmPassword">ÎπÑÎ∞ÄÎ≤àÌò∏ ÌôïÏù∏</label>
          <input
            type="password"
            id="confirmPassword"
            v-model="confirmPassword"
            required
            placeholder="ÎπÑÎ∞ÄÎ≤àÌò∏Î•º Îã§Ïãú ÏûÖÎ†•ÌïòÏÑ∏Ïöî"
          />
        </div>
        <button type="submit" :disabled="loading || !isPasswordMatch" class="register-btn">
          {{ loading ? 'ÌöåÏõêÍ∞ÄÏûÖ Ï§ë...' : 'ÌöåÏõêÍ∞ÄÏûÖ' }}
        </button>
      </form>
      <div class="form-footer">
        <p>Ïù¥ÎØ∏ Í≥ÑÏ†ïÏù¥ ÏûàÏúºÏã†Í∞ÄÏöî? <router-link to="/login">Î°úÍ∑∏Ïù∏</router-link></p>
      </div>
      <div v-if="error" class="error-message">
        {{ error }}
      </div>
      <div v-if="!isPasswordMatch && confirmPassword" class="error-message">
        ÎπÑÎ∞ÄÎ≤àÌò∏Í∞Ä ÏùºÏπòÌïòÏßÄ ÏïäÏäµÎãàÎã§.
      </div>
    </div>
  </div>
</template>

<script>
import { ref, computed } from 'vue'
import { useRouter } from 'vue-router'
import { authStore } from '../store/auth'

export default {
  name: 'RegisterPage',
  setup() {
    const router = useRouter()
    const userData = ref({
      username: '',
      email: '',
      displayName: '',
      password: ''
    })
    const confirmPassword = ref('')
    const loading = ref(false)
    const error = ref('')

    const isPasswordMatch = computed(() => {
      return !confirmPassword.value || userData.value.password === confirmPassword.value
    })

    const handleRegister = async () => {
      if (!isPasswordMatch.value) {
        error.value = 'ÎπÑÎ∞ÄÎ≤àÌò∏Í∞Ä ÏùºÏπòÌïòÏßÄ ÏïäÏäµÎãàÎã§.'
        return
      }

      loading.value = true
      error.value = ''
      
      try {
        await authStore.register(userData.value)
        router.push('/')
      } catch (err) {
        if (err.response?.status === 400) {
          error.value = 'Ïù¥ÎØ∏ Ï°¥Ïû¨ÌïòÎäî ÏÇ¨Ïö©ÏûêÎ™Ö ÎòêÎäî Ïù¥Î©îÏùºÏûÖÎãàÎã§.'
        } else {
          error.value = 'ÌöåÏõêÍ∞ÄÏûÖÏóê Ïã§Ìå®ÌñàÏäµÎãàÎã§. Îã§Ïãú ÏãúÎèÑÌï¥Ï£ºÏÑ∏Ïöî.'
        }
      } finally {
        loading.value = false
      }
    }

    return {
      userData,
      confirmPassword,
      loading,
      error,
      isPasswordMatch,
      handleRegister
    }
  }
}
</script>

